import React, {Component} from "react"
import { connect } from "react-redux"
import { Route, Link } from 'react-router-dom';
class Checkin extends Component{
	constructor(props){
		super(props)
	}
	

	render(){
		console.log(this.props.location);
		return(
					
					<form id = "checkIn" className = "container-fluid">
					<link to = "/users"></link>
					<button>  Facebook</button>
					<div className = "lines"><hr className = "line" /><span className = "line-Word"></span><hr className = "line"  /></div>
					<input type = "email"  className = "email" placeholder = ".   . " />
					<input type = "text" className = "fi" placeholder = "  " />
					<input type = "text" className = "userName" placeholder = " " />
					<input type = "password" className = "password" placeholder = "" />	
					<button className = "checkIn-button"></button>
					<div>,    ,         cookie.</div>
					<div>{this.props.children}</div>
					</form>
				
			)
	}
}
export default Checkin


